<template>
  <div>
        <div class="col-md-12 col-sm-12 col-xs-12">
          <v-form v-model="valid" ref="form">
          <div class="mb-5 mt-10">
            <div class="title">[]     </div>
            <v-divider></v-divider>
          </div>
          <div>
            <v-expansion-panels accordion hover>
              <v-expansion-panel
              >
                <v-expansion-panel-header @click.native="expansionPanel()">
                  <div>
                    <v-checkbox v-model="checkbox1" label="  ()" @click.native="check($event)"
                    :rules="[(v) => !!v || '     .']"
                    />
                  </div>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  '()YA global'( '')   , <br />
                    "           .<br />
                             <br />
                        . <br />
                       ( )   .<br />
                  -   2017 8 24  .<br /><br />
                  1.    <br />
                         .<br />         .
                </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel>
                <v-expansion-panel-header>
                  <div>
                    <v-checkbox v-model="checkbox2" label="  ()" @click.native="check($event)"
                    :rules="[(v) => !!v || '     .']"
                    />
                  </div>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                    ,   ,    ,      .
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </div>
          <div>
            <!-- <v-flex xs12 sm8 offset-sm2> -->
            <v-flex xs12 sm12>
              <div class="mb-5 mt-10">
            <div class="title">  </div>
            <v-divider></v-divider>
          </div>
          <v-layout column>
            <v-flex>
              <v-text-field
                id="name"
                name="name"
                type="text"
                label=""
                v-model="user.name"
                :rules="nameRules"
              ></v-text-field>
            </v-flex>
            <v-flex>
              <v-text-field
                id="email"
                name="email"
                type="email"
                label="e-mail"
                v-model="user.email"
                :rules="emailRules"
              ></v-text-field>
            </v-flex>
            <v-flex>
              <v-text-field
                id="tel"
                name="tel"
                type="tel"
                label="( )"
                v-model="user.tel"
                :rules="telRules"
              ></v-text-field>
            </v-flex>
            <v-flex>
              <v-textarea
                id="etc"
                name="etc"
                type="etc"
                label="     ."
                v-model="user.etc"
                autocomplete="off"
              ></v-textarea>
            </v-flex>
            <v-flex text-xs-center mt-5>
              <v-btn color="secondary" @click="submit"> </v-btn>
            </v-flex>
          </v-layout>

      </v-flex>
          </div>
          </v-form>
        </div>

    <v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline"> </v-card-title>

        <v-card-text>
          {{message}}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <!-- <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
          >
            Disagree
          </v-btn> -->

          <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
            :loading="loading"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from 'querystring';
export default {
  data() {
    return {
      checkbox1: false,
      checkbox2: false,
      user: {},
      valid: false,
      dialog: false,
      nameRules: [
        v => !!v || '  .',
        v => v && v.length <= 10 || ' 10    .',
      ],
      emailRules: [
        v => !!v || 'E-mail  .',
        v => /.+@.+/.test(v) || ' E-mail   .',
      ],
      telRules: [
        v => !!v || ' .',
        v => /^\d+$/.test(v) || '  .',
      ],
      loading: false,
      message: '',
      formUrl: 'https://docs.google.com/forms/u/0/d/e/1FAIpQLSf9BL3Jz48qeflSD1jscadlyuG7VXiNcxVyj85ZR8TFc_E2wQ/formResponse',
      formEntry: {
        name: 'entry.1571130719',
        email: 'entry.1032548777',
        tel: 'entry.323479861',
        etc: 'entry.37639584'
      }
    }
  },
  methods: {
    check: function(e) {
      e.cancelBubble = true;
    },
    expansionPanel: function() {
    },
    async submit() {
      if (this.$refs.form.validate()) {
        const url =          this.formUrl;
        const body = {
          [this.formEntry.name]: this.user.name,
          [this.formEntry.email]: this.user.email,
          [this.formEntry.tel]: this.user.tel,
          [this.formEntry.etc]: this.user.etc,
        };
        try {
          this.loading = true;
          await this.postData(url, body);
          this.reset();
          this.message = ' .';
        } catch (error) {
          this.message = error.message;
        } finally {
          this.loading = false;
          this.dialog = true;
        }
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    postData(url, data) {
      return fetch(url, {
        method: 'POST',
        mode: 'no-cors',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: qs.stringify(data),
      });
    },
  }
}
</script>



